import React, { useState, useEffect } from 'react';
import { Container, Row, Col, Button, Badge, Offcanvas, Toast, ToastContainer } from 'react-bootstrap';
import TopNavbar from './components/TopNavbar';
import Sidebar from './components/Sidebar';
import PatientSidebar from './components/PatientSidebar';
import Dashboard from './pages/Dashboard';
import Patients from './pages/Patients';
import Appointments from './pages/Appointments';
import Feedback from './pages/Feedback';
import Login from './pages/Login';
import Register from './pages/Register';

// Patient-side components
import PatientDashboard from './pages/PatientDashboard';
import AppointmentBooking from './pages/AppointmentBooking';
import PatientQuestionnaire from './pages/PatientQuestionnaire';
import DoctorRating from './pages/DoctorRating';
import MedicationTracker from './pages/MedicationTracker';

const App = () => {
  const [currentUser, setCurrentUser] = useState(null);
  const [activeSection, setActiveSection] = useState('dashboard');
  const [showRegister, setShowRegister] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const [userRole, setUserRole] = useState('doctor'); // 'doctor' or 'patient'
  const [showMobileSidebar, setShowMobileSidebar] = useState(false);
  const [isMobile, setIsMobile] = useState(window.innerWidth <= 768);
  const [showToast, setShowToast] = useState(false);
  const [toastMessage, setToastMessage] = useState('');
  const [toastType, setToastType] = useState('success');

  // Enhanced mobile detection
  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 768);
      if (window.innerWidth > 768) {
        setShowMobileSidebar(false);
      }
    };

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  console.log('App component loaded', { currentUser, isLoading, userRole });

  // Check for existing user session on app load
  useEffect(() => {
    const token = localStorage.getItem('mediAssistToken');
    const userData = localStorage.getItem('mediAssistUser');
    
    if (token && userData) {
      try {
        const user = JSON.parse(userData);
        setCurrentUser(user);
      } catch (error) {
        console.error('Error parsing user data:', error);
        localStorage.removeItem('mediAssistToken');
        localStorage.removeItem('mediAssistUser');
      }
    }
    setIsLoading(false);
  }, []);

  const handleLogin = (user) => {
    setCurrentUser(user);
    setUserRole(user.role || 'doctor');
    setActiveSection(user.role === 'patient' ? 'patient-dashboard' : 'dashboard');
  };

  const handleLogout = () => {
    localStorage.removeItem('mediAssistToken');
    localStorage.removeItem('mediAssistUser');
    setCurrentUser(null);
    setUserRole('doctor');
    setActiveSection('dashboard');
    if (isMobile && offcanvasRef.current) {
      const offcanvas = new window.bootstrap.Offcanvas(offcanvasRef.current);
      offcanvas.hide();
    }
  };

  // Mobile utility functions
  const handleTouchStart = (e) => {
    setTouchStart({ x: e.touches[0].clientX, y: e.touches[0].clientY });
  };

  const handleTouchMove = (e) => {
    if (!touchStart) return;
    
    const currentX = e.touches[0].clientX;
    const currentY = e.touches[0].clientY;
    const deltaX = currentX - touchStart.x;
    const deltaY = currentY - touchStart.y;
    
    // Right swipe to open sidebar
    if (deltaX > 50 && Math.abs(deltaY) < 100 && !showMobileNav && touchStart.x < 50) {
      setShowMobileNav(true);
    }
    // Left swipe to close sidebar
    else if (deltaX < -50 && Math.abs(deltaY) < 100 && showMobileNav) {
      setShowMobileNav(false);
    }
  };

  const handleTouchEnd = () => {
    setTouchStart(null);
  };

  const handleRegister = (user) => {
    setCurrentUser(user);
    setUserRole(user.role || 'doctor');
    setActiveSection(user.role === 'patient' ? 'patient-dashboard' : 'dashboard');
    setShowRegister(false);
  };

  const switchRole = (newRole) => {
    setUserRole(newRole);
    setActiveSection(newRole === 'patient' ? 'patient-dashboard' : 'dashboard');
  };

  const renderActiveSection = () => {
    switch (activeSection) {
      // Doctor/Admin Dashboard Sections
      case 'dashboard':
        return <Dashboard />;
      case 'patients':
        return <Patients />;
      case 'appointments':
        return <Appointments />;
      case 'feedback':
        return <Feedback />;
      
      // Patient Dashboard Sections
      case 'patient-dashboard':
        return <PatientDashboard />;
      case 'book-appointment':
        return <AppointmentBooking />;
      case 'questionnaire':
        return <PatientQuestionnaire />;
      case 'rate-doctor':
        return <DoctorRating />;
      case 'medications':
        return <MedicationTracker />;
      
      default:
        return userRole === 'patient' ? <PatientDashboard /> : <Dashboard />;
    }
  };

  // Show loading screen while checking authentication
  if (isLoading) {
    return (
      <div className="loading-screen d-flex justify-content-center align-items-center min-vh-100 bg-primary text-white">
        <div className="text-center">
          <div className="mb-4">
            <h1 className="display-4 fw-bold">🏥 Medi-Assist</h1>
            <p className="fs-5">Healthcare Management Dashboard</p>
          </div>
          <div className="spinner-border text-light mb-3" role="status">
            <span className="visually-hidden">Loading...</span>
          </div>
          <p>Loading application...</p>
        </div>
      </div>
    );
  }

  // Show authentication screens if not logged in
  if (!currentUser) {
    return (
      <div className="min-vh-100">
        {showRegister ? (
          <Register 
            onRegister={handleRegister}
            onSwitchToLogin={() => setShowRegister(false)}
          />
        ) : (
          <Login 
            onLogin={handleLogin}
            onSwitchToRegister={() => setShowRegister(true)}
          />
        )}
      </div>
    );
  }

  // Main dashboard interface
  return (
    <div 
      className="min-vh-100 d-flex flex-column"
      onTouchStart={handleTouchStart}
      onTouchMove={handleTouchMove}
      onTouchEnd={handleTouchEnd}
    >
      <TopNavbar user={currentUser} onLogout={handleLogout} />
      
      <div className="flex-grow-1 d-flex">
        {/* Mobile Menu Button */}
        <Button
          variant="outline-primary"
          className="d-lg-none position-fixed"
          style={{ top: '80px', left: '15px', zIndex: 1050 }}
          onClick={() => setShowMobileSidebar(true)}
        >
          <i className="bi bi-list"></i>
        </Button>

        {/* Role Switcher - Hidden on mobile for cleaner look */}
        <div className="position-fixed d-none d-md-block" style={{ top: '70px', right: '20px', zIndex: 1040 }}>
          <div className="d-flex gap-2 mb-2">
            <Button 
              size="sm" 
              variant={userRole === 'doctor' ? 'primary' : 'outline-primary'}
              onClick={() => switchRole('doctor')}
              className="d-flex align-items-center"
            >
              <i className="bi bi-hospital me-1 d-none d-lg-inline"></i>
              <span className="d-none d-lg-inline">Doctor</span>
              <span className="d-lg-none">Dr</span>
              {userRole === 'doctor' && <Badge bg="light" text="dark" className="ms-1">●</Badge>}
            </Button>
            <Button 
              size="sm" 
              variant={userRole === 'patient' ? 'success' : 'outline-success'}
              onClick={() => switchRole('patient')}
              className="d-flex align-items-center"
            >
              <i className="bi bi-person-heart me-1 d-none d-lg-inline"></i>
              <span className="d-none d-lg-inline">Patient</span>
              <span className="d-lg-none">Pt</span>
              {userRole === 'patient' && <Badge bg="light" text="dark" className="ms-1">●</Badge>}
            </Button>
          </div>
        </div>

        {/* Desktop Sidebar */}
        <div className="d-none d-lg-block">
          {userRole === 'patient' ? (
            <PatientSidebar 
              activeSection={activeSection} 
              setActiveSection={setActiveSection}
              onLogout={handleLogout}
            />
          ) : (
            <Sidebar 
              activeSection={activeSection} 
              setActiveSection={setActiveSection}
              onLogout={handleLogout}
            />
          )}
        </div>

        {/* Mobile Sidebar (Offcanvas) */}
        <Offcanvas 
          show={showMobileSidebar} 
          onHide={() => setShowMobileSidebar(false)}
          placement="start"
          className="bg-light"
        >
          <Offcanvas.Header closeButton className="bg-primary text-white">
            <Offcanvas.Title>
              <i className="bi bi-heart-pulse me-2"></i>
              Medi-Assist
            </Offcanvas.Title>
          </Offcanvas.Header>
          <Offcanvas.Body className="p-0">
            {/* Role Switcher in Mobile Menu */}
            <div className="p-3 border-bottom">
              <div className="d-flex gap-2">
                <Button 
                  size="sm" 
                  variant={userRole === 'doctor' ? 'primary' : 'outline-primary'}
                  onClick={() => {
                    switchRole('doctor');
                    setShowMobileSidebar(false);
                  }}
                  className="flex-fill"
                >
                  <i className="bi bi-hospital me-1"></i>
                  Doctor
                </Button>
                <Button 
                  size="sm" 
                  variant={userRole === 'patient' ? 'success' : 'outline-success'}
                  onClick={() => {
                    switchRole('patient');
                    setShowMobileSidebar(false);
                  }}
                  className="flex-fill"
                >
                  <i className="bi bi-person-heart me-1"></i>
                  Patient
                </Button>
              </div>
            </div>
            
            {/* Mobile Sidebar Content */}
            {userRole === 'patient' ? (
              <PatientSidebar 
                activeSection={activeSection} 
                setActiveSection={(section) => {
                  setActiveSection(section);
                  setShowMobileSidebar(false);
                }}
                onLogout={handleLogout}
                isMobile={true}
              />
            ) : (
              <Sidebar 
                activeSection={activeSection} 
                setActiveSection={(section) => {
                  setActiveSection(section);
                  setShowMobileSidebar(false);
                }}
                onLogout={handleLogout}
                isMobile={true}
              />
            )}
          </Offcanvas.Body>
        </Offcanvas>
        
        <main className="flex-grow-1 bg-light pb-5 pb-lg-0" style={{ marginLeft: '0' }}>
          {/* Mobile-optimized Header */}
          <div className="bg-white border-bottom">
            <Container fluid className="py-2 py-lg-3">
              <Row className="align-items-center">
                <Col xs={12} md={6} className="ps-5 ps-lg-3">
                  <nav aria-label="breadcrumb">
                    <ol className="breadcrumb mb-0">
                      <li className="breadcrumb-item text-muted small">Medi-Assist</li>
                      <li className="breadcrumb-item active small" aria-current="page">
                        {activeSection.charAt(0).toUpperCase() + activeSection.slice(1).replace('-', ' ')}
                      </li>
                    </ol>
                  </nav>
                </Col>
                <Col xs={12} md={6} className="text-md-end mt-2 mt-md-0">
                  <div className="d-flex align-items-center justify-content-end gap-2 gap-md-3">
                    <Badge bg={userRole === 'patient' ? 'success' : 'primary'} className="px-2 px-md-3 py-1 py-md-2 small">
                      <i className={`bi bi-${userRole === 'patient' ? 'person-heart' : 'hospital'} me-1`}></i>
                      <span className="d-none d-sm-inline">
                        {userRole === 'patient' ? 'Patient Portal' : 'Doctor Dashboard'}
                      </span>
                      <span className="d-sm-none">
                        {userRole === 'patient' ? 'Patient' : 'Doctor'}
                      </span>
                    </Badge>
                    <small className="text-muted d-none d-md-inline">
                      Welcome, <strong>{currentUser?.name || currentUser?.email}</strong>
                    </small>
                  </div>
                </Col>
              </Row>
            </Container>
          </div>
          
          {/* Main Content */}
          <Container fluid className="py-2 py-md-4 px-2 px-md-3">
            <div className="fade-in">
              {renderActiveSection()}
            </div>
          </Container>

          {/* Mobile Bottom Navigation */}
          <div className="mobile-bottom-nav d-lg-none position-fixed bottom-0 start-0 end-0 bg-white border-top shadow-lg">
            <Container fluid className="p-0">
              <Row className="g-0">
                {userRole === 'patient' ? (
                  <>
                    <Col className="text-center">
                      <Button
                        variant="link"
                        className={`mobile-nav-btn ${activeSection === 'patient-dashboard' ? 'active' : ''}`}
                        onClick={() => setActiveSection('patient-dashboard')}
                      >
                        <i className="bi bi-house-door d-block"></i>
                        <small>Home</small>
                      </Button>
                    </Col>
                    <Col className="text-center">
                      <Button
                        variant="link"
                        className={`mobile-nav-btn ${activeSection === 'book-appointment' ? 'active' : ''}`}
                        onClick={() => setActiveSection('book-appointment')}
                      >
                        <i className="bi bi-calendar-plus d-block"></i>
                        <small>Book</small>
                      </Button>
                    </Col>
                    <Col className="text-center">
                      <Button
                        variant="link"
                        className={`mobile-nav-btn ${activeSection === 'patient-appointments' ? 'active' : ''}`}
                        onClick={() => setActiveSection('patient-appointments')}
                      >
                        <i className="bi bi-calendar-check d-block"></i>
                        <small>Visits</small>
                      </Button>
                    </Col>
                    <Col className="text-center">
                      <Button
                        variant="link"
                        className={`mobile-nav-btn ${activeSection === 'medications' ? 'active' : ''}`}
                        onClick={() => setActiveSection('medications')}
                      >
                        <i className="bi bi-capsule d-block"></i>
                        <small>Meds</small>
                      </Button>
                    </Col>
                  </>
                ) : (
                  <>
                    <Col className="text-center">
                      <Button
                        variant="link"
                        className={`mobile-nav-btn ${activeSection === 'dashboard' ? 'active' : ''}`}
                        onClick={() => setActiveSection('dashboard')}
                      >
                        <i className="bi bi-grid d-block"></i>
                        <small>Dashboard</small>
                      </Button>
                    </Col>
                    <Col className="text-center">
                      <Button
                        variant="link"
                        className={`mobile-nav-btn ${activeSection === 'appointments' ? 'active' : ''}`}
                        onClick={() => setActiveSection('appointments')}
                      >
                        <i className="bi bi-calendar2-week d-block"></i>
                        <small>Schedule</small>
                      </Button>
                    </Col>
                    <Col className="text-center">
                      <Button
                        variant="link"
                        className={`mobile-nav-btn ${activeSection === 'patients' ? 'active' : ''}`}
                        onClick={() => setActiveSection('patients')}
                      >
                        <i className="bi bi-people d-block"></i>
                        <small>Patients</small>
                      </Button>
                    </Col>
                    <Col className="text-center">
                      <Button
                        variant="link"
                        className="mobile-nav-btn"
                        onClick={() => setShowMobileSidebar(true)}
                      >
                        <i className="bi bi-three-dots d-block"></i>
                        <small>More</small>
                      </Button>
                    </Col>
                  </>
                )}
              </Row>
            </Container>
          </div>
        </main>
      </div>
      
      {/* Footer */}
      <footer className="bg-dark text-light py-3 mt-auto">
        <Container>
          <Row className="align-items-center">
            <Col md={6}>
              <p className="mb-0">&copy; 2025 Medi-Assist | Healthcare Management Dashboard</p>
            </Col>
            <Col md={6} className="text-md-end">
              <small className="text-muted">Improving patient care through technology 🚀</small>
            </Col>
          </Row>
        </Container>
      </footer>
    </div>
  );
};

export default App;
